body.dark {
  background-image: none;
  background-color: #090e1e !important;
  color: #fff !important;
}

body.dark main aside.left-navigation {
  border-right: none;
  background: #343a40;
  border-right: 1px solid #212529;
}

body.dark input.form-control,
body.dark select.form-control,
body.dark textarea,
body.dark table,
body.dark table th,
body.dark table td,
body.dark div.card,
body.dark aside,
body.dark nav.card,
body.dark ul.pagination li.page-item a.page-link,
body.dark footer,
body.dark div.modal-content,
body.dark div.list-group,
body.dark a.list-group-item {
  background-color: #090e1e !important;
  color: #fff !important;
}

body.dark div.card,
body.dark div.modal-content,
body.dark a.list-group-item {
  border: 1px solid #fff !important;
}

body.dark .dropdown-menu {
  border: 1px solid #fff !important;
  background-color: #090e1e !important;
  color: #fff !important;
}

body.dark a, body.dark i.bi {
  color: #fff !important;
}

body.dark .dropdown-item:hover {
  background-color: #2e3a49 !important;
}

body.dark ul.pagination li.page-item.active a.page-link {
  color: #212529 !important;
  background-color: #fff !important;
  border-color: transparent;
}

body.dark footer {
  border-top: 0;
}

body {
  overflow-x: hidden;
  background-image: linear-gradient(to right, #079c56 0%, #090e1e 50%);
}